import { NavLink } from "react-router-dom";

import {
  Button,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";

import { logoutUser } from "store/profile/asyncThunks";

import { useAppDispatch } from "hooks/useAppDispatch";

import { useForm } from "hooks/useForm";

import styles from "./style.module.scss";

const Profile = () => {
  const dispatch = useAppDispatch();

  const { formData, handleChange } = useForm({
    name: "",
    email: "",
    password: "",
  });

  const logoutUserHandler = async () => await dispatch(logoutUser());

  return (
    <div className={styles.profileBlock}>
      <nav className={styles.navBlock}>
        <NavLink
          to="/profile"
          className={`${styles.link} ${styles.link__active} text text_type_main-medium`}
        >
          <span className="ml-2 pb-5"></span>
        </NavLink>

        <NavLink
          to="/profile/orders"
          className={`${styles.link} text text_type_main-medium text_color_inactive`}
        >
          <span className="ml-2 pb-5"> </span>
        </NavLink>

        <NavLink
          to="/login"
          className={`${styles.link} text text_type_main-medium text_color_inactive`}
          onClick={logoutUserHandler}
        >
          <span className="ml-2 pb-5"></span>
        </NavLink>

        <div className="pt-20">
          <span className="text text_type_main-default text_color_inactive">
                 <br />
               
          </span>
        </div>
      </nav>

      <form>
        <Input
          type={"text"}
          placeholder={""}
          onChange={handleChange}
          icon={"EditIcon"}
          value={formData.name}
          name={"name"}
          size={"default"}
          extraClass="mb-5"
        />

        <Input
          type={"text"}
          placeholder={"Email"}
          onChange={handleChange}
          icon={"EditIcon"}
          name={"email"}
          value={formData.email}
          extraClass="mb-5"
        />

        <Input
          type={"password"}
          placeholder={""}
          onChange={handleChange}
          icon={"EditIcon"}
          name={"password"}
          value={formData.password}
          size={"default"}
          extraClass="mb-5"
        />

        <div className={styles.profileFormFooter}>
          <Button htmlType="button" type="secondary" size="medium">
            <p className="text text_type_main-default"></p>
          </Button>

          <Button htmlType="button" type="primary" size="medium">
            <p className="text text_type_main-default"></p>
          </Button>
        </div>
      </form>
    </div>
  );
};

export default Profile;
